.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #E5E6E8;
  font-weight: bold;

  &__top {
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__content {
    padding-inline: 20px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: inherit;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    letter-spacing: 2px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 4px;
    border-bottom: 1px solid #292929;

    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    letter-spacing: 2px;
  }
}



