.contacts {
  &__wrapper {
    @include page-grid;

    grid-row: 48px;
  }

  &__form {
    grid-column: span 2;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: 1 / 6;
    }

    input::placeholder, textarea::placeholder {
      font-size: 14px;
      font-weight: 400;
      line-height: 140%;
    }
  }

  &__input {
    width: 100%;
    padding: 14px 16px;
    box-sizing: border-box;

    background-color: #F8F8FA;
    color: #7E7E83;

    margin-bottom: 16px;

    border-radius: $default-border-radius;
    border: 1px solid transparent;

    &::placeholder {
      color: #7E7E83;
    }

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: 1 / 6;
    }

    &:hover {
      border: 1px solid #D5D7DE;
    }

    &:focus {
      border: 1px solid #292929;
      outline: none;
    }
  }

}
